<template>
    <div class="comments">
        <div class="content_lm">
            <div class="control_comments">
                <div class="praise">
                    <i class="el-icon-caret-top icon"></i>
                    <span class="text">{{comments.vote_count}}人已赞过</span>
                </div>
                <div class="share">
                    <h4 class="share_title">分享到：</h4>
                    <ul>
                        <li>
                            <a href="#" class="el-icon-message"></a>
                        </li>
                        <li>
                            <a href="#" class="el-icon-message"></a>
                        </li>
                        <li>
                            <a href="#" class="el-icon-message"></a>
                        </li>
                        <li>
                            <a href="#" class="el-icon-message"></a>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="pub_hr"></div>
            <div class="content_commtents">
                <div class="input_comments">
                    <div class="img">
                        <img src="http://lorempixel.com/640/480/?25659" title='作者是谁' alt='作者是谁'>
                    </div>
                    <div class="textarea">
                        <textarea :rows="rows" placeholder="说点什么..." v-model="textarea" class="textInput" @focus='inputFocus' @blur='inputBlur'></textarea>
                    </div>
                    <div class="send">
                    	<el-button type="primary" v-show='sendFlag'>评论</el-button>
                    	<div v-show="!sendFlag">登录后可以发表评论</div>
                    </div>
                </div>
                <div class="hr"></div>
                <ul class="comments_list">
                    <li v-for='item in content.data'>
                        <div class="img">
                            <img :src="item.users.avatar" :alt="item.users.name" :title="item.users.name">
                        </div>
                        <div class="comments_list_content">
                        	<div class="top_info">
                        		<h4 class="username">{{item.users.name.slice(0,20)}}</h4>
                        		<span class="time">{{item.users.name}}</span>
                        	</div>
                        	<div class="text markdown-body">
                        		{{item.body.slice(0,450)}}
                        	</div>
                        </div>
                    </li>
                </ul>
            </div>
            <div class="pub_hr"></div>
        </div>
    </div>
</template>
<script>
export default {
    created() {
            let slug = this.$route.query.slug;
            this.tecthData(slug);
            this.getComments(slug);
        },
        data() {
            return {
                per_page: 10,
                content: '',
                comments: '',
                textarea: '',
                rows: 1,
                sendFlag: false
            }
        },
        methods: {
            getComments(slug) {
                var _this = this;
                this.comments = '';
                this.$axios.get('/articles/' + slug).then(response => {
                    let result = response.data.data;
                    _this.comments = result;
                });

            },
            tecthData(slug) {
                this.content = '';
                this.$axios.get('/articles/' + slug + '/replies?per_page=' + this.per_page).then(response => {
                    let result = response.data;
                    this.content = result;
                });
            },
            inputFocus() {
                this.rows = 4;
                if (this.textarea.length > 1) {
                    this.sendFlag = true;
                }
            },
            inputBlur() {
            	if(this.textarea.length>1){return false;}
                this.rows = 1;
                this.sendFlag = false;
            }
        },
        watch: {
            textarea: function(val, oldVal) {
                if (this.textarea.length > 1) {
                    this.sendFlag = true;
                }
            },
        }
}
</script>
<style lang="scss" scoped>
@import 'comments.scss';
</style>



